import { getArticle, getComments, addComment, deleteComment } from '@/api/article'
import ArticleMeta from './components/ArticleMeta'
import { mapState } from 'vuex'
import MarkdownIt from 'markdown-it'

export default {
  name: 'ArticleIndex',
  components: {
    ArticleMeta
  },
  computed: {
    ...mapState(['user'])
  },
  data () {
    return {
      content: ''
    }
  },
  async asyncData ({ params }) {
    const { slug } = params
    try {
      const [articleRes, commentsRes] = await Promise.all([
        getArticle(slug),
        getComments(slug)
      ])
      const article = articleRes.data.article
      const md = new MarkdownIt()
      article.body = md.render(article.body)
      return {
        article: articleRes.data.article,
        comments: commentsRes.data.comments
      }
    } catch (error) {
      console.log(error)
    }
  },
  methods: {
    async handleSubmit () {
      try {
        const { params } = this.$route
        const { slug } = params
        const content = this.content
        await addComment(slug, { comment: { body: content } })
        this.content = ''
        this.getComment()
      } catch (error) {
        console.log(error)
      }
    },
    async getComment () {
      try {
        const { params } = this.$route
        const { slug } = params
        const { data } = await getComments(slug)
        this.comments = data.comments
      } catch (error) {
        console.log(error)
      }
    },
    async handleDeleteComment (comment) {
      try {
        const { id } = comment
        const { params: { slug } } = this.$route
        await deleteComment(slug, id)
        const { data } = await getComments(slug)
        this.comments = data.comments
      } catch (error) {
        console.log(error)
      }
    }
  }
}
